export default {
  name: 'TestResult',
  props: {
    total: {
      type: Number,
      default: 0,
    },
    count: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    percent() {
      return `rotate(${(360 / this.total) * this.count}deg)`
    },
    maskStatus() {
      return (360 / this.total) * this.count > 180 ? '#FFC651' : 'white'
    },
    outsideStatus() {
      return (360 / this.total) * this.count > 180
        ? 'unset'
        : 'polygon(50% 0%, 100% 0%, 100% 100%, 50% 100%)'
    },
    score() {
      return (100 / this.total) * this.count
    },
  },
}
